.div26 {
  width: 295px;
  position: relative;
  font-weight: 900;
  display: inline-block;
}
.frame-icon {
  width: 70px;
  border-radius: var(--br-3xs);
  height: 86px;
}
.div27,
.pdf {
  align-self: stretch;
  position: relative;
  text-align: left;
}
.pdf {
  font-size: var(--font-size-xl);
  font-weight: 900;
  font-family: var(--font-montserrat);
  color: var(--color-black);
}
.div27 {
  font-size: var(--font-size-xs);
  font-family: var(--font-montserrat-alternates);
  color: var(--color-gray);
  display: inline-block;
  height: 89px;
  flex-shrink: 0;
}
.frame-wrapper,
.pdf-parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.pdf-parent {
  align-self: stretch;
  gap: var(--gap-12xs);
}
.frame-wrapper {
  width: 195px;
}
.frame-parent6 {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-11xl) var(--padding-lg) var(--padding-7xs)
    var(--padding-4xl);
  background-color: var(--color-white);
  align-self: stretch;
  border-radius: var(--br-3xs);
  box-sizing: border-box;
  height: 153px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-36xl);
}
.powerpoint-file-svgrepo-com-1-icon {
  width: 70px;
  position: relative;
  height: 70px;
  overflow: hidden;
  flex-shrink: 0;
}
.powerpoint-file-svgrepo-com-1-parent {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-11xl) var(--padding-lg) 0 var(--padding-4xl);
  background-color: var(--color-white);
  align-self: stretch;
  border-radius: var(--br-3xs);
  box-sizing: border-box;
  height: 153px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-36xl);
}
.frame-parent5 {
  align-self: stretch;
  gap: 26px;
}
.frame-parent5,
.frame5,
.th-page2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.frame5 {
  width: 365px;
  gap: 14px;
}
.th-page2 {
  width: 100%;
  position: relative;
  background-color: var(--color-white);
  height: 844px;
  overflow: hidden;
  padding: 83px var(--padding-xs);
  box-sizing: border-box;
  text-align: center;
  font-size: 28px;
  color: var(--color-black);
  font-family: var(--font-montserrat);
}
